<template>
  <div id="app" class="text-center">
    <div id="wrapper">
      <div id="page-wrapper">
        <div class="container-fluid">

          <!-- Page Heading -->
          <!-- /.row -->
          <div class="row center-block">
            <div class="col-md-8">
              <div class="panel with-nav-tabs panel-primaryes">
                <div class="panel-heading">
                  <ul class="nav nav-tabs">
                    <li v-for="i in tabs.name.length"
                        :class="[tab == tabs.name[i-1].toLowerCase()? 'active' : 'dropdown']"
                        @click="tab=tabs.name[i-1].toLowerCase()"><a data-toggle="tab"><span
                      :class="tabs.fa[i-1]"></span> {{tabs.name[i - 1]}}</a></li>
                  </ul>
                </div>

                <div class="panel-body">
                  <div class="tab-content">
                    <div class="tab-pane fade in active">
                      <component :is="tab" :qrcode="qrcode"></component>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="col-md-4">
              <div class="container-fluid">
                <div class="row">
                  <img v-if="getUrl == 0"
                       src="https://www.qr-code-generator.com/wp-content/themes/qr/images/banners/frames/websiteQRCode_noFrame.png"
                       style="width:230px; height:230px; opacity: 0.2"/>
                  <a ref="embeded" rel='nofollow' style='cursor:default; border: 0'>
                    <img v-if="getUrl != 0" :src="getUrl"/>
                  </a>
                </div>
                <div class="row">
                  <button class="btn btn-info" @click="printImg">Print</button>
                </div>
                <div class="row">
                  <a class="btn btn-info" :href="getUrl" target="_blank" download>Download</a>
                </div>
                <div class="row">
                  <a @click="emb" data-toggle="modal" data-target="#exampleModal" role="button">Embeded qr code</a>
                </div>
              </div>
            </div>
          </div>

        </div>
      </div>
      <!-- /#page-wrapper -->
    </div><!-- /#wrapper -->
    <!-- Modal -->
    <div class="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
         aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h3 class="modal-title" id="exampleModalLabel">Embed QR code on your website</h3>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <p>To embed the QR code on your website, use the following HTML code. Commercial usage is allowed.</p>
          <div class="modal-body">
            <textarea class="form-control" rows="5" id="comment" v-model="embeded"></textarea>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-success" data-dismiss="modal">Close</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue';
  import {mix} from './mixins'
  import Sms from './components/Sms.vue'
  import Email from './components/Email.vue'
  import Text from './components/Text.vue'
  import Url from './components/Url.vue'
  import Vcard from './components/Vcard.vue'

  export default {
    data() {
      return {
        qrcode: {
          sms: {
            phone: '',
            text: ''
          },
          email: {
            text: '',
            email: '',
            subject: ''
          },
          text: {
            test: ''
          },
          link: {
            url: ''
          },
          vcard: {
            firstname: '',
            lastname: '',
            phone: '',
            fax: '',
            email: '',
            company: '',
            job: '',
            city: '',
            street: '',
            state: '',
            country: ''
          },
          url: ''
        },
        tabs: {
          name: ['Url', 'VCard', 'Message', 'Email', 'SMS'],
          fa: ['fas fa-external-link-alt', 'fas fa-address-card', 'fa fa-align-left', 'fas fa-at', 'fas fa-envelope']
        },
        tab: 'url',
        code: null,
        embeded: ''
      }
    },
    components: {
      url: Url,
      vcard: Vcard,
      message: Text,
      email: Email,
      sms: Sms
    },
    updated() {
      localStorage.setItem('qr-barcode-generator', JSON.stringify(this.qrcode));
      localStorage.setItem('qr-barcode-generator-tab', this.tab);
    },
    computed: {
      getUrl() {
        return this.getUnProcessedUrl.replace(/ /g, '%20').replace(new RegExp("\n", "gm"), "%0A")
      },
      getUnProcessedUrl() {
        let baseUrl = 'https://chart.googleapis.com/chart?cht=qr&chl='
        let newValue = this.qrcode;
        let tab = this.tab;
        let url = tab === 'url' ? `${newValue.link.url}&chs=230x230&choe=UTF-8&chld=L|2` :
          tab === 'email' ? `MATMSG%3ATO%3A${newValue.email.email}%3BSUB%3A${newValue.email.subject}%3BBODY%3A${newValue.email.text}%3B%3B&chs=230x230&choe=UTF-8&chld=L|2` :
            tab === 'sms' ? `SMSTO%3A${newValue.sms.phone}%3A${newValue.sms.text}&chs=230x230&choe=UTF-8&chld=L|2` :
              tab === 'message' ? `${newValue.text.text}&chs=230x230&choe=UTF-8&chld=L|2` :
                tab === 'vcard' ? `BEGIN%3AVCARD%0AVERSION%3A3.0%0AN%3A${newValue.vcard.lastname}%3B${newValue.vcard.firstname}%0AFN%3A${newValue.vcard.firstname}%20${newValue.vcard.lastname}%0AORG%3A${newValue.vcard.company}%0ATITLE%3A${newValue.vcard.job}%0AADR%3A%3B%3B${newValue.vcard.street}%3B${newValue.vcard.city}%3B${newValue.vcard.state}%3B%3B${newValue.vcard.country}%0ATEL%3BWORK%3BVOICE%3A${newValue.vcard.phone}%0ATEL%3BFAX%3A${newValue.vcard.fax}%0AEMAIL%3BWORK%3BINTERNET%3A${newValue.vcard.email}%0AEND%3AVCARD%0A&chs=230x230&choe=UTF-8&chld=L|2` : ''
        return baseUrl + url
      }

    },
    mounted() {
      let code = JSON.parse(localStorage.getItem('qr-barcode-generator'));
      let tab = localStorage.getItem('qr-barcode-generator-tab')
      if (code && tab) {
        this.qrcode = code
        this.tab = tab
      }
    },
    methods: {
      printImg() {
        var win = window.open('');
        win.document.write('<img src="' + this.getUrl + '" onload="window.print();window.close()" />');
        win.focus();
      },
      emb() {
        this.embeded = this.$refs.embeded.outerHTML.replace(new RegExp("amp;", "gm"), "")
      }
    }
  }
</script>




